            </div>
            <div class="card-body px-0 pb-2" >
                <div class="table-responsive p-0">
                    <div class="text-end">
                    <button class="btn btn-info ms-2 me-2" data-bs-toggle="modal" data-bs-target="#processModal"></button>
                    </div>
                <table class="table align-items-center mb-2 table-hover">
                  <tr>
                    <th class="text-left text-uppercase text-secondary font-weight-bolder opacity-10">LOT</th>
