<template>
  <div class="container-fluid">

    <div class="row mt-3">
      <div class="col text-end">
        <button class="btn btn-success" @click="selectOption"></button>
      </div>
    </div>

    <div class="card my-4">
      <div class="card-header p-0 position-relative mt-n4 mx-3 z-index-2">
        <div class="bg-gradient-success shadow-success border-radius-lg pt-3 pb-2">
          <h6 class="text-white text-capitalize ps-3"></h6>
        </div>
      </div>
      <div class="row g-2 my-3 px-3">
        <div class="col-md-3">
          <div class="d-flex align-items-center">
            <label class="form-label me-2 mb-0 " style="width: 100px;"></label>
            <input v-model="searchName" type="text" class="form-control border text-center" placeholder="" />
          </div>
        </div>
        <div class="col-md-3">
          <div class="d-flex align-items-center">
            <label class="form-label me-2 mb-0 " style="width: 100px;">ID</label>
            <input v-model="searchId" type="text" class="form-control border text-center" placeholder="ID" />
          </div>
        </div>
      </div>
    </div>

    <div class="row">
      <div class="col-md-8">
        <div class="card my-4">
          <div class="card-header p-0 position-relative mt-n4 mx-3 z-index-2">
            <div class="bg-gradient-success shadow-success border-radius-lg pt-3 pb-2">
              <h6 class="text-white text-capitalize ps-3"></h6>
            </div>
          </div>
          <div class="card-body px-0 pb-2">
            <div class="table-responsive mb-4" style="max-height: 600px; overflow-y: auto;">
              <table class="table align-items-center mb-0">
                <thead>
                  <tr>
                    <th class="align-middle text-center">ID</th>
                    <th class="align-middle text-center"></th>
                    <th class="align-middle text-center"></th>
                    <th class="align-middle text-center"></th>
                    <th class="align-middle text-center"></th>
                    <th class="align-middle text-center"></th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="(item, index) in optionList" v-bind:key="option_id">
                    <td class="align-middle text-center">{{ item.option_id }}</td>
                    <td class="align-middle text-center">{{ item.option_name }}</td>
                    <td class="align-middle text-center">{{ item.option_standard }}</td>
                    <td class="align-middle text-center">{{ codeToName(item.option_operator, codes) }}</td>
                    <td class="align-middle text-center">{{ item.option_method }}</td>
                    <td class="align-middle text-center">
                      <button class="btn btn-warning m-0" v-on:click="editOption(index)"></button>
                      <!-- <button class="btn btn-danger m-0 ms-2" v-on:click="delOption(index)"></button> -->
                    </td>
                  </tr>
                  <tr v-if="optionList.length === 0">
                    <td colspan="5" class="text-center">  </td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>

      <div class="col-md-4">
        <div class="card p-3">
          <h5>{{ action }}</h5>
          <div class="mb-3 d-flex align-items-center">
            <label class="form-label me-2 mb-0" style="width: 100px;">ID</label>
            <input v-model="selected.option_id" type="text" class="form-control border text-center" readonly />
          </div>
          <div class="mb-3 d-flex align-items-center">
            <label class="form-label me-2 mb-0" style="width: 100px;"></label>
            <input v-model="selected.option_name" type="text" class="form-control border text-center" />
          </div>
          <div class="mb-3 d-flex align-items-center">
            <label class="form-label me-2 mb-0" style="width: 100px;"></label>
            <input v-model="selected.option_standard" type="text" class="form-control border text-center" />
          </div>
          <div class="mb-3 d-flex align-items-center">
            <label class="form-label me-2 mb-0" style="width: 100px;"></label>
            <select v-model="selected.option_operator" class="form-select border text-center">
              <option v-for="code in codes" :key="code.sub_code" :value="code.sub_code">
                {{ code.sub_code_name }}
              </option>
            </select>
          </div>
          <div class="mb-3 d-flex align-items-center">
            <label class="form-label me-2 mb-0" style="width: 100px;"></label>
            <input v-model="selected.option_method" type="text" class="form-control border text-center" />
          </div>
          <div class="text-end">
            <button class="btn btn-info m-0" @click="save"></button>
            <button class="btn btn-secondary m-0 ms-2" @click="resetForm"></button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>


<script>
  import axios from 'axios';
  import { codeToName } from '../../utils/common';

  // pinia import
  // stores 
  import { useUserStore } from "@/stores/user"; 
  // state, getter => mapState 
  // actions => mapActions 
  import { mapState } from 'pinia';

  export default {
    data() {
      return {
        action: '', // (/)
        optionList: [],
        selected: {},
        searchName: '',
        searchId: '',
        codes: [],
      };
    },
    computed: {
      // ...mapState(store, []), ...mapActions(store, [])
      // stores    
      //     computed     
      //    userInfo    
      //     
      // <template></template>  userInfo.employee_id
      // export default {}  this.userInfo.employee_id
      ...mapState(useUserStore, [
      "isLoggedIn",
      "userInfo",
    ])
    },
    created() {
      // this.selectOption();
      this.getOptionType();
    },
    methods: {
      // 
      async selectOption() {

        let params = {};

        if (this.searchId) params.option_id = this.searchId;
        if (this.searchName) params.option_name = this.searchName;

        let ajaxRes =
          await axios.get(`/api/options`, {
            params
          })
          .catch(err => console.log(err));
        this.optionList = ajaxRes.data;
      },

      // ()
      async editOption(index) {
        this.selected =  {
          ...this.optionList[index]
        }
        this.action = '';
      },

      //  (/)
      async save() {
        let result = await axios.post('/api/options', this.selected)
          .catch(err => console.log(err));
        console.log(result);

        if (result.data.affectedRows > 0) {
          alert(' ');
          this.selectOption();
          this.resetForm();
        } else {
          alert('   ');
        }
      },

      // 
      // async delOption(index) {
      // },

      //  
      resetForm() {
        this.selected = {};
        this.action = '';
      },

      async getOptionType() {
        let res = await axios.get(`/api/codes/C2`)
          .catch(err => console.log(err));
        this.codes = res.data;
      },

      codeToName(code, codeArray) {
        return codeToName(code, codeArray);
      },
    },
  };
</script>