    <div class="row">
      <!--   div-->
      <div class="col-12">
        <!--  div-->
        <div class="card my-4">
          <!-- div-->
          <div class="card-header p-0 position-relative mt-n4 mx-3 z-index-2">
            <div class="bg-gradient-success shadow-success border-radius-lg pt-4 pb-3">
              <h6 class="text-white text-capitalize ps-3"></h6>
            </div>
          </div>
          <div class="card-body px-0 pb-2">
            <div class="table-responsive p-0">
              <table class="table align-items-center mb-0">
                <thead>
                  <tr>
                    <th class="text-center text-uppercase text-secondary text-xxs font-weight-bolder opacity-10">No</th>
                    <th class="text-center text-uppercase text-secondary text-xxs font-weight-bolder opacity-10">
                    </th>
                    <th class="text-center text-uppercase text-secondary text-xxs font-weight-bolder opacity-10">
                    </th>
                    <th class="text-center text-uppercase text-secondary text-xxs font-weight-bolder opacity-10">
                    </th>
                    <th class="text-center text-uppercase text-secondary text-xxs font-weight-bolder opacity-10">
                    </th>
                    <th class="text-center text-uppercase text-secondary text-xxs font-weight-bolder opacity-10">
                    </th>
                    <th class="text-center text-uppercase text-secondary text-xxs font-weight-bolder opacity-10"></th>
                    <th class="text-center text-uppercase text-secondary text-xxs font-weight-bolder opacity-10"></th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="(info, index) in matReqList" v-bind:key="info.id" v-on:click="mateDetailInfo(info)">
                    <td class="align-middle font-weight-bolder text-center">{{ index + 1 }}</td>
                    <td class="align-middle font-weight-bolder text-center">{{ info.req_date }}</td>
                    <td class="align-middle font-weight-bolder text-center">{{ info.req_id }}</td>
                    <td class="align-middle font-weight-bolder text-center">{{ info.vendor_name }}</td>
                    <td class="align-middle font-weight-bolder text-center">{{ info.employee_name }}</td>
                    <td class="align-middle font-weight-bolder text-center">{{ info.req_due_date }}</td>
                    <td class="align-middle font-weight-bolder text-center">{{ info.memo }}</td>
                    <td class="align-middle font-weight-bolder text-center"><button class="btn btn-sm btn-success" disabled>{{ info.req_status }}</button></td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
      <!-- row div-->
    </div>
    <div class="row">
      <div class="col-12">
        <div class="card my-4">
          <div class="card-header p-0 position-relative mt-n4 mx-3 z-index-2">
            <div class="bg-gradient-success shadow-success border-radius-lg pt-4 pb-3">
              <h6 class="text-white text-capitalize ps-3"></h6>
            </div>
          </div>
          <div class="card-body px-0 pb-2">
            <div class="table-responsive p-0">
              <table class="table align-items-center justify-content-center mb-0">
                <thead>
                  <tr>
                    <th class="text-center text-uppercase text-secondary text-xxs font-weight-bolder opacity-10">No</th>
                    <th class="text-center text-uppercase text-secondary text-xxs font-weight-bolder opacity-10">ID
                    </th>
                    <th class="text-center text-uppercase text-secondary text-xxs font-weight-bolder opacity-10">
                    </th>
                    <th class="text-center text-uppercase text-secondary text-xxs font-weight-bolder opacity-10">
                    </th>
                    <th class="text-center text-uppercase text-secondary text-xxs font-weight-bolder opacity-10">
                    </th>
                    <th class="text-center text-uppercase text-secondary text-xxs font-weight-bolder opacity-10"></th>
                  </tr>

                </thead>
                <tbody>
                    <tr v-for="(info, index) in mateList" v-bind:key="info.id">
                      <td class="align-middle font-weight-bolder text-center">{{ index + 1 }}</td>
                      <td class="align-middle font-weight-bolder text-center">{{ info.req_detail_id }}</td>
                      <td class="align-middle font-weight-bolder text-center">{{ info.mate_name }}</td>
                      <td class="align-middle font-weight-bolder text-center">{{ info.req_amount }}</td>
                      <td class="align-middle font-weight-bolder text-center">
                      <input type="number" v-model.number="info.inbound_amount" @change="amountCheck(info)"></td>
                      <td>
                      <input type="text" v-model="info.memo" class="form-control text-center" placeholder=" ">
                      </td>
                      </tr>
                </tbody>
              </table>
            </div>
            <!--      -->
            <div v-if="mateList.length > 0" class="table-responsive p-0">
              <table class="table align-items-center justify-content-center mb-0"></table>
              <div class="text-end pe-5 mt-3">
                <button class="btn btn-success" @click="storeAdd"></button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
  // stores 
  import { useUserStore } from "@/stores/user"; 
  // state, getter => mapState 
  // actions => mapActions 
  import { mapState } from 'pinia';

export default {
  name: "orderprod",
  data() {
    return {
      matReqList: [],
      mateList: [],

      // 
      selectedList: [],
      mate_id: [],
      vendor_id: [],
    }
  },
  computed: {
    ...mapState(useUserStore, [
      "isLoggedIn",
      "userInfo",
    ])
  },
  created() {
    this.storeMateAll();
  },
  methods: {
    //  
    storeMateAll() {
      axios
        .get('/api/storeMate', {
          params: this.search,
        })
        .then((response) => {
          this.matReqList = response.data;
        })
        .catch((error) => {
          console.error(' :', error);
        });
    },

    //    
    mateDetailInfo(info) {
      this.selectedInfo = info;
      axios
        .get(`/api/storeMate/${info.req_id}`, {
        })
        .then((response) => {
          this.mateList = response.data;
        })
        .catch((error) => {
          console.log(' :', error.response?.data || error.message);
        });
    },
      //       (  )
     amountCheck(info) {
     if (info.inbound_amount > info.req_amount) {
      alert("    .");
      info.inbound_amount = info.req_amount; //   
    }
    },

    //   
    async storeAdd() {
      //      
      this.selectedList = this.mateList.filter(item => item.inbound_amount > 0);

      if (this.selectedList.length === 0) {
        alert("   .");
        return;
      }

      const storeInfo = {
        store_detail_list: this.selectedList.map(item => ({
          req_id: item.req_id,
          mate_id: item.mate_id,
          inbound_amount: item.inbound_amount,
          memo: item.memo || '',
        })),
        employee_id: this.userInfo.employee_id,
        vendor_id: this.selectedInfo.vendor_id, //   
      };

      try {
        const ajaxRes = await axios.post(`/api/storeSave`, storeInfo);
        if (ajaxRes.data.affectedRows > 0) {
          alert(".");
          // this.$router.push('/StoreList');

          this.mateList = [];
          this.id = '';
          this.storeMateAll();
        } else {
          alert(" .");
        }
      } catch (err) {
        console.error(err);
        alert("   .");
      }
    }
  }
}
</script>