<template>

<div class="text-end mb-3">
  <button @click="handleSearch" class="btn btn-success me-2 shadow rounded-pill">
     
  </button>
  <button class="btn btn-danger me-2 shadow rounded-pill" @click="goToMatmaPage">
     
  </button>
  <button class="btn btn-info shadow rounded-pill" @click="confirmMate">
     
  </button>
</div>

  <!--  -->
  <div class="row">
    <div class="card my-4">
      <div class="card-header p-0 position-relative mt-n4 mx-3 z-index-2">
        <div class="bg-gradient-success shadow-success border-radius-lg pt-4 pb-3">
          <h6 class="text-white text-capitalize ps-3"></h6>
        </div>
      </div>

      <div class="card-body">
        <ul class="list-group list-group-horizontal">
          <li class="list-group-item" style="margin-left: 10px;"></li>
          <li class="list-group-item"><input type="text" v-model="search.vendor_name"></li>
          <li class="list-group-item" style="margin-left: 20px;"></li>
          <li class="list-group-item"><input type="date" v-model="search.startDate"> ~ <input type="date" v-model="search.endDate"></li>
          <!-- <li class="list-group-item" style="margin-left: 20px;"></li>
          <li class="list-group-item"><input type="text" v-model="search.orderNumber"></li> -->
        </ul>

        <ul class="list-group list-group-horizontal flex-wrap mt-3">
          <!-- <li class="list-group-item me-3 d-flex align-items-center" style="border-left: 1px solid #ccc;"></li> -->
          <!-- <li class="list-group-item me-3 d-flex align-items-center" style="border-left: 1px solid #ccc;">
            <input type="text" v-model="search.prod_name" class="form-control me-2" style="border: 1px solid #ccc; box-sizing: border-box;"> -->
            <!-- <i class="fas fa-search" style="font-size: 20px; cursor: pointer;"></i> -->
          <!-- </li> -->
          <!--  -->
          <!-- <li class="list-group-item me-3 d-flex align-items-center" style="border-left: 1px solid #ccc;"></li>
          <li class="list-group-item me-3 d-flex align-items-center" style="border-left: 1px solid #ccc;">
              <input type="text" v-model="search.supplier" class="form-control me-2" style="border: 1px solid #ccc; box-sizing: border-box;"/> -->
            <!-- <i class="fas fa-search" style="font-size: 20px; cursor: pointer;"></i> -->
          <!-- </li> -->

            <!--  -->
          <li class="list-group-item me-3 d-flex align-items-center" style="border-left: 1px solid #ccc;"></li>
          <li class="list-group-item me-3 d-flex align-items-center" style="border-left: 1px solid #ccc;">
          <select v-model="search.req_status" class="form-select" style="min-width: 130px;">
            <option disabled value="" style="text-align: center;"></option>
            <option value="1o"></option>
            <option value="2o"></option>
            <option value="3o"></option>
          </select>
          <!--   -->
          <!-- <i class="fas fa-search d-flex align-items-center" style="font-size: 20px; cursor: pointer;"></i> -->
          </li>
            
          
        </ul>
      </div>
    </div>
  </div>

  <!--  -->
  <div class="container-fluid py-4">
    <div class="row">
      <div class="col-12">
        <div class="card my-4">
          <div class="card-body px-0 pb-2">
            <div class="table-responsive p-0">
              <table class="table align-items-center mb-0">
                <thead>
                  <tr>
                    <th>No</th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                  </tr>
                </thead>
                <tbody>
                  <template v-if="matReqList.length > 0">
                    <tr v-for="(info, index) in matReqList" :key="info.id" @click="handleRowClick(info)">
                      <td>{{ index + 1 }}</td>
                      <td>           
                        <input type="checkbox" v-if="info.req_status === ''" v-model="info.selected" @change="check"></td>
                      <td>{{ info.req_date }}</td>
                      <td>{{ info.req_id }}</td>
                      <td>{{ info.vendor_name }}</td>
                      <td>{{ info.employee_name }}</td>
                      <td>{{ info.req_due_date }}</td>
                      <td class="align-middle text-center">
                      <button class="btn btn-sm" :class="{
                                                          'btn-primary': info.req_status === '',
                                                          'btn-success': info.req_status === '',
                                                          'btn-secondary': info.req_status === ''
                                                          }"disabled>{{ info.req_status === '' ? '' : info.req_status === '' ? '' : '' }} {{ info.req_status }} </button></td>
                      <td>{{ info.memo }}</td>
                      <td>{{ info.confirm_date }}</td>
                      <td>{{ info.manager_name }}</td>
                    </tr>
                  </template>
                  <tr v-else>
                    <td colspan="11" style="text-align: center;"> .</td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>

    <!--   -->
    <div class="container-fluid py-4">
      <div class="row">
        <div class="col-12">
          <div class="card my-4">
            <div class="card-body px-0 pb-2">
              <div class="table-responsive p-0">
                <table class="table align-items-center mb-0">
                  <thead>
                    <tr>
                      <th>No</th>
                      <th>ID</th>
                      <th>ID</th>
                      <th></th>
                      <th></th>
                      <th></th>
                    </tr>
                  </thead>
                  <tbody>
                    <!-- <template v-if="count > 0 "> -->
                      <tr v-for="(info, index) in mateList" :key="info.id" style="cursor: pointer;">
                        <td>{{ index + 1 }}</td>
                        <td>{{ info.req_detail_id }}</td>
                        <td>{{ info.req_id }}</td>
                        <td>{{ info.mate_id }}</td>
                        <td>{{ info.req_amount }}</td>
                        <td>{{ info.memo }}</td>
                      </tr>
                    <!-- </template> -->
                    <!-- <tr v-else>
                      <td colspan="4">   </td>
                    </tr> -->
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
</template>
    
<script>
import axios from 'axios';
import MaterialCheckbox from '../../components/MaterialCheckbox.vue';
  // stores 
  import { useUserStore } from "@/stores/user"; 
  // state, getter => mapState 
  // actions => mapActions 
  import { mapState } from 'pinia';

export default {
  name: 'MaterialManagement',
  components: {
    MaterialCheckbox,
  },
  data() {
    return {
      search: {
      },
      matReqList: [],
      selectedInfo: {},        //   
      mateList: [],
      allSelected: [],
    };
  },
    created() {
    this.handleSearch();
  },
  methods: {
    
      check() {
        this.allSelected = this.matReqList.every(item => item.selected);
      },
    async handleSearch() {
      //    API  .
      await axios
        .get('/api/materials', {
          params: this.search,
        })  
        .then((response) => {
          this.matReqList = response.data.map(item => ({
          ...item,
          selected: false
        }));
        this.allSelected = false;
        })
        .catch((error) => {
          console.error(' :', error);
        });
    },
    async handleRowClick(info) {
      this.selectedInfo = info;
      await axios
        .get(`/api/materials/${info.req_id}`,{
        })
        .then((response) => {
          this.mateList = response.data;
        })
        .catch((error) => {
          console.log(' :', error.response?.data || error.message);
        });
    },
    goToMatmaPage() {
      this.$router.push('/matma'); // Vue Router   
    },

    async confirmMate() {
      const selectedItems = this.matReqList.filter(item => item.selected);

      let params = {
        mates: selectedItems,
        employee_id: this.userInfo.employee_id,
      }
      
      if (selectedItems.length > 0) {
          if (confirm('  ?')) {
            let res = await axios.post(`/api/mateConfirm`, params)
              .catch(err => console.log(err));
            if (res.data.success) {
              alert('  .');
              this.handleSearch();
            } else {
              alert('    .');
            }
          }
        } else {
          alert('  .');
        }
    }
    
  },
  computed: {
    ...mapState(useUserStore, [
      "isLoggedIn",
      "userInfo",
    ])
  },

};
</script>